.array-container {
  position : center;    
  width    : 100%;
  height   : 100%;
}

.array-bar {
  width: 10px;
  display: inline-block;
  margin: 5px 5px;
}

.button {
  background-color: #516da1;
  border: none;
  color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 10px 10px;
  cursor: pointer;
}