body {
  margin : 0;
  background-color: #2b2b2b;
}

.App {
  text-align: center;
}

.outer-container {
  position : absolute;
  display: table;
  width: 100%; 
  height: 50%; 
}

.inner-container {
  display: table-cell;
  vertical-align: bottom;
  text-align: center;
}

.centered-content {
  display: inline-block;
  text-align: left;
  padding : 20px;
  border : 0px transparent;
}

